import React from "react";
import { Select } from "antd";
import { useQuery } from "react-query3";
import { Framework } from "../../types";

export default function FrameworkSelectBox(props) {
  const { data, isLoading } = useQuery<Framework[]>("/dashboardframework");
  return (
    <Select {...props} loading={isLoading}>
      {data?.map((framework) => (
        <Select.Option key={framework.name} value={framework.id}>{`${framework.name} [${framework.version}]`}</Select.Option>
      ))}
    </Select>
  );
}
